/*  HomePage from src  */
* {
  padding: 0rem;
  margin: 0rem;
  user-select: none;
}

::-webkit-scrollbar {
  display: none;
}

body {
  background: url("./Back.jpg") no-repeat fixed;
  background-size: cover;
  /* overflow: hidden; */
}

.container {
  color: #fff;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container .topBanner {
  background: yellow;
  height: 1rem;
  width: 80%;
}

.container .mainBody {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container .mainBody strong {
  font-size: 2rem;
  margin: 1rem;
  font-family: sans-serif;
}

.container .mainBody .postersRow {
  display: flex;
  align-items: center;
}

.container .mainBody .inputField {
  display: flex;
  margin: 1rem;
  padding: 2rem;
  width: 50vw;
}

.container .mainBody .inputField input {
  font-family: verdana;
  text-align: right;
  border: none;
  outline: none;
  font-size: 1.5rem;
  width: 1px;
  padding: 0.5rem 0rem;
  background-color: black;
  caret-color: yellow;
  overflow: hidden;
}

.loginWithGoogle:nth-child(1)> :first-child {
  border-radius: 2rem !important;
}

.words {
  font-family: verdana;
  background: black;
  border: none;
  overflow: hidden;
  font-size: 1.5rem;
  padding: 0.5rem 0rem;
  display: flex;
  justify-content: right;
  width: 50%;
  background: black;
  white-space: nowrap;
  border-radius: 1rem 0rem 0rem 1rem;
}

.words span {
  /* margin-left: 0.2rem; */
  color: rgb(130, 130, 130);
  margin-right: 0.5rem;
}

.incomingWords {
  font-family: verdana;
  border: none;
  outline: none;
  font-size: 1.5rem;
  padding: 0.5rem 0rem;
  overflow: hidden;
  background: black;
  color: rgb(130, 130, 130);
  white-space: nowrap;
  border-radius: 0rem 1rem 1rem 0rem;
  width: 50%;
}

#inProgress {
  padding: 0;
  margin: 0;
  color: white;
}

.correctTyping {
  text-decoration: none !important;
}

/*  Poster from component   */
#timer {
  background: black;
  color: white;
}

.wrongWord {
  text-decoration: line-through;
}

.poster {
  background: yellow;
  padding: 1rem;
  color: #000;
  border-radius: 0.5rem;
  margin: 1rem;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  font-family: system-ui;
}

/*  givenPara from component   */
.wrongTyping {
  text-decoration: line-through;
  color: white;
}

.timeSelector {
  background-color: black;
  color: white;
  border-radius: 0.5rem;
  width: 80%;
}

.givenPara {
  color: #fff;
  border: solid 0.1rem rgb(207, 168, 11);
  border-radius: 1rem;
  width: 50rem;
  padding: 1rem;
  margin: 1rem;
  line-height: 1rem;
  font-weight: 400;
  font-family: sans-serif;
  line-height: 1.5rem;
  font-weight: bold;
}

.Res h4 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  height: 10rem;
  width: 20rem;
  background: hsla(0, 0%, 100%, 0.879);
  box-shadow: 0 0 10px 100rem rgba(0, 0, 0, 0.701);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  flex-direction: row;
  padding: 1rem;
  z-index: 10;
}

.Res h4 div {
  color: #000 !important;
  margin: 1rem;
  text-align: center;
}

.Res button {
  top: 10px;
  right: 20px;
  position: absolute;
  background: #ff0;
  color: #000;
  border: none;
  padding: 0.5rem;
  border-radius: 50%;
  box-shadow: 0 0 10px #000;
  width: 2rem;
  font-weight: 700;
}

.GoogleLogin {
  width: 100px;
  border-radius: 2rem !important;
  margin: 1.5rem 0rem 0rem 0rem !important;
}

.GoogleLogout {
  width: 100px;
  border-radius: 2rem !important;
  margin: 1.5rem 0rem 0rem 0rem !important;
}

.GoogleLogin * {
  border-radius: 2rem !important;
  color: #000;
  font-weight: 700 !important;
  margin: 0 !important;
  font-size: 14px !important;
  padding: 0rem 0.2rem !important;
}

.GoogleLogout * {
  border-radius: 2rem !important;
  color: #000;
  font-weight: 700 !important;
  margin: 0 !important;
  font-size: 14px !important;
  padding: 0rem 0.2rem !important;
}

.disclaimer {
  display: none;
}

@media screen and (max-width:450px) {
  body {
    width: 100vw;
  }

  strong {
    font-size: x-large !important;
    width: 20rem;
  }

  .mainBody {
    width: 100vw;
    text-align: center;
  }

  .postersRow {
    display: grid !important;
    grid-template-columns: auto auto;
  }

  .givenPara {
    width: fit-content;
    font-size: 0.7rem !important;
  }

  .inputField {
    width: 80vw !important;
    margin: 0 !important;
  }
}